define("org/forgerock/openidm/ui/common/login/LoginView", ["underscore", "org/forgerock/commons/ui/common/LoginView", "org/forgerock/openidm/ui/common/util/AMLoginUtils"], function (_, commonLoginView, amLoginUtils) {

    var LoginView = function LoginView() {},
        obj;

    LoginView.prototype = commonLoginView;

    obj = new LoginView();

    obj.render = function (args, callback) {
        var amCallback = amLoginUtils.init(this, true);

        commonLoginView.render.call(this, args, _.bind(function () {
            if (callback) {
                callback();
            }

            if (amCallback) {
                amCallback();
            }
        }, this));
    };

    return obj;
});

