define("org/forgerock/openidm/ui/common/components/Footer", ["jquery", "lodash", "org/forgerock/commons/ui/common/components/Footer", "org/forgerock/commons/ui/common/main/Configuration", "org/forgerock/openidm/ui/common/delegates/InfoDelegate"], function ($, _, Footer, Configuration, InfoDelegate) {
    function isAdmin() {
        return Configuration.loggedUser && Configuration.loggedUser.has("roles") && _.indexOf(Configuration.loggedUser.uiroles, "ui-admin") > -1;
    }

    var Component = Footer.extend({
        getVersion: function getVersion() {
            return InfoDelegate.getVersion().then(function (data) {
                return data.productVersion + " (" + $.t("openidm.admin.revision") + ": " + data.productRevision + ")";
            });
        },
        showVersion: function showVersion() {
            return isAdmin();
        }
    });

    return new Component();
});

